<template>
  <!--  //开票信息-->
  <div class="DrawerMessage">
    <div class="HomePageTop">
      <div class="shade"></div>
      <div class="Head">
        <i class="iconfont pa left24" @click="prev"></i>
        <div class="inputfather">
          <i class="iconfont">&#xe61a;</i>
          <input type="text" placeholder="搜索我的订单" style=""></div>
        <span  @click="EditInformation" class="icon_text f28">添加</span>
      </div>
    </div>
    <van-tabs v-model="active">
      <van-tab title="开票资料">
        <DrawerMessage1 ref="first"></DrawerMessage1>
      </van-tab>
      <van-tab title="已经开的发票">
        <DrawerMessage2></DrawerMessage2>
      </van-tab>
      <van-tab title="未开发票">
        <DrawerMessage3></DrawerMessage3>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
  import Vue from "vue"
  import DrawerMessage1 from "./DrawerMessageHome/DrawerMessage1";
  import DrawerMessage2 from "./DrawerMessageHome/DrawerMessage2";
  import DrawerMessage3 from "./DrawerMessageHome/DrawerMessage3";
  export default {
    name: "DrawerMessage",
    components: {DrawerMessage3, DrawerMessage2, DrawerMessage1},
    data() {
      return {

        active: 0,


      }
    },
    methods: {
      prev(){

          this.$router.go(-1)

      },
      //点击添加跳转并且传入一些参数
      EditInformation(){
        // console.log(this.$refs.first.SelectText);
        this.$router.push({
          name:"EditInformation",
          params:{SelectText:this.$refs.first.SelectText,id:1},
        })
      },
    },
    created() {

    }
  }
</script>

<style lang="less">
  html,
  body {
    background: #F4F6F9;
  }
  .DrawerMessage{

    .HomePageTop{background: #fff}
    .Head{
      .icon_text{
        font-siez:.28rem;
        position: absolute;
        right: .24rem;
      }
      background: #fff;
      .iconfont{position: absolute;left: .24rem}
      .inputfather{
        position: relative;display: inline-block;padding-left: 0.84rem; border: 1px solid rgb(255, 153, 0); height: 0.66rem; line-height: 0.66rem;
        i{
           width: 0.4rem; height: 0.4rem; position: absolute; left: 0.24rem;color: #ccc;
        }
        input{
          height: 0.4rem; width: 4.42rem; border: none; font-size: 0.28rem; color: rgb(204, 204, 204);    vertical-align: 5%;
        }
      }

    }
  }
  .van-tabs__line{
    background: #f90;    width: 0.36rem;
  }
.van-tabs__content{
  width: 100%;
  height: 100%;
}
  .van-cell{
   /*line-height: 1.02rem;*/
    height: 1.02rem;
    padding: 0.27rem .3rem;
  }
.van-collapse-item__wrapper{
  border-bottom: none;
}
[class*=van-hairline]::after{
  border-bottom: none;

}
  .van-cell{
    border-bottom: 1px solid #e3e4e8;
  }
  .van-cell::after{
    border: none;
  }
  .van-collapse-item__content{
    padding-bottom: 0;
  }
  .van-dialog {
    padding-top: 0.48rem;
    border-radius: inherit;
    font-size: 0.38rem;
  }
  .van-tab--active span{
    font-weight: bold !important;
    font-size: .34rem;

  }
</style>



